export function askDeviceMotion(funcArg) {
  try {
    if (
      DeviceMotionEvent &&
      typeof DeviceMotionEvent.requestPermission === 'function'
    ) {
      DeviceMotionEvent.requestPermission()
        .then(response => {
          if (response === 'granted') {
            grantedDeviceMotion(funcArg)
          }
        })
        .catch(console.error)
    } else {
      grantedDeviceMotion(funcArg)
    }
  } catch (oops) {
    alert('Your device and application combination do not support device motion events.')
  }
}

export function grantedDeviceMotion(funcArg) {
  window.addEventListener(
    'devicemotion',
    funcArg,
    false
  );
}

export function grantedDeviceOrientation(funcArg, statefulObj) {
  window.startingPoint = null;
  window.addEventListener(
    'deviceorientation',
    funcArg.deviceOrientation,
    false
  );
  window.addEventListener(
    'orientationchange',
    funcArg.orientationChange,
    false
  );
  statefulObj.setState({capabilities:true});
}

export function askDeviceOrientation(funcArg, statefulObj) {
  try {
    if (
      DeviceOrientationEvent &&
      typeof DeviceOrientationEvent.requestPermission === 'function'
    ) {
      DeviceOrientationEvent.requestPermission()
        .then(response => {
          if (response === 'granted') {
            // alert('granted device permission');
            grantedDeviceOrientation(funcArg, statefulObj)
          } else {
            alert('You will need to restart the browser and give permission for device orientation access to use this.')
            // alert('not granted device permission');
            // non iOS 13+
          }
        })
        .catch(console.error)
    } else {
      // alert('attempting device permission');
      grantedDeviceOrientation(funcArg, statefulObj)
    }
  } catch (oops) {
    alert('Your device and application combination do not support device orientation events.')
  }
}
